.header {
    position:sticky;
    margin: 0px 10px;
    padding: 5px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    color:black;
    height: 50px;
}

.header_info {
    display: flex;
    align-items: center;
}

.header_right {
    display: flex;
    align-items: center;
}

.header_search{
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: #f4f4f9;
    width: 700px;
    height:45px;
    border-radius: 5px;
}

.header_search > input {
    border: none;
    outline: none;
    background: transparent;
    width:100%;
    height:40px;
}
.info{
    color: #5f6368;
    font-size:22px;
    padding-left:5px;
}